import { MoState as State } from '@ali/4ever-cangjie';

export default {
  name: 'highlight',

  onOpenTag: (state: State, name: string, _attrs, id: string): boolean => {
    if (name !== 'mark') return false;

    state.addMarks({
      id,
      value: {
        highlight: '#FFC000',
      }
    });
    return true;
  },

  onCloseTag: (state: State, name: string, _attrs, id: string): boolean => {
    if (name !== 'mark') return false;
    state.removeMarks({ id });
    return true;
  },
};
